.h-slider {
  --v-h-slider-indent: 0;
  --v-h-slider-item-padding: 2.6667rem;
  --v-h-slider-pagination-margin-left: 2rem;
  --v-h-slider-pagination-margin-bottom: 2.5333rem;
  --v-h-slider-title-margin-top: 1.3333rem;
  --v-h-slider-title-margin-bottom: 2rem;
  position: relative;
  overflow: hidden;
  aspect-ratio: 1;
  width: 100%;
  max-height: 450px;
}
.h-slider__content {
  position: relative;
  height: 100%;
  z-index: 2;
}
.h-slider__item {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-width: 440px;
}
.h-slider__container {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: var(--v-h-slider-item-padding);
}
.h-slider__img {
  position: absolute;
  top: var(--v-h-slider-indent);
  right: var(--v-h-slider-indent);
  bottom: var(--v-h-slider-indent);
  left: var(--v-h-slider-indent);
  z-index: -1;
}
.h-slider__img * {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.h-slider__subtitle {
  line-height: 1.2;
  font-size: 0.7333rem;
  color: var(--v-secondary);
}
.h-slider__title {
  margin-top: var(--v-h-slider-title-margin-top);
  margin-bottom: var(--v-h-slider-title-margin-bottom);
  line-height: 1;
}
.h-slider__pagination {
  --swiper-pagination-color: var(--v-yellow);
  --swiper-pagination-bullet-size: 0.6667rem;
  --swiper-pagination-bullet-inactive-color: transparent;
  --swiper-pagination-bullet-horizontal-gap: 0.6667rem;
  text-align: start;
  margin: 0 0 var(--v-h-slider-pagination-margin-bottom) var(--v-h-slider-pagination-margin-left);
  height: 0;
}
.h-slider__pagination .swiper-pagination-bullet {
  border: 2px solid var(--v-white);
}
.h-slider__pagination .swiper-pagination-bullet-active {
  border: none !important;
}